.login-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70vh;
  }
  
  .login-page div {
    margin: 1rem;
  }
  
  .login-page h3 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .login-page form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    max-width: 500px;
    background-color: #fff;
    padding: 3rem;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  }
  
  .login-page label {
    display: block;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    margin-right: 0.5rem;
  }
  
  .login-page input {
    width: 100%;
    font-size: 1rem;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: none;
    border-bottom: 2px solid #ccc;
  }
  
  .login-submit-btn {
    padding: 0.5rem 1rem;
    background-color: #1484e6;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .login-submit-btn:hover {
    background-color: #3e8e41;
  }
  
  